import uniPopupDialog from '@/components/uni-popup/uni-popup.vue'
import util from '@/utils/http.js'
import {
	regExpTel,
	getQueryString,
	toast
} from '../../utils/index.js'
export default {
	components: {},
	data() {
		return {
			loginFlag: false,
			account: '',
			inviteCode: '',
		};
	},
	onShow() {
	
		if (uni.getStorageSync('accountInfo').token) {
			this.loginFlag = true;
			this.account = uni.getStorageSync('accountInfo').account;
			this.inviteCode = uni.getStorageSync('accountInfo').inviteCode;
		}
	},
	components: {
		uniPopupDialog
	},
	methods: {
		open() {
			this.$refs.popup.open()
		},
		linktask(val) {
			uni.navigateTo({
				url: '/pages/index/releaseTask?type=' + val
			});
		},
		linkorder() {
			uni.switchTab({
				url: '/pages/index/order'
			});
		},
		linkrecharge() {
			uni.navigateTo({
				url: '/pages/index/recharge'
			});
		},
		toSubPage(path){
						if(path.indexOf('logOut') >= 0){
							this.logOut();
						} else {
							util.navigateToPath(path)
						}
					},
		// 退出登录
		logOut() {
			uni.showModal({
				title: '确定要退出登录吗？',
				success: (res) => {
					if (res.confirm) {
						// uni.showToast({
						// 	title: '退出登录',
						// 	icon: 'none'
						// })
						uni.navigateTo({
							url:'/pages/login'
						})
						const userinfo = {
							token: '',
							account: '',
							photo: '',
							inviteCode: '暂无',
						}
						uni.setStorageSync('accountInfo', userinfo);
					}
					
				}
			})
		},
		// logout() {
		//





		//
		// 	uni.redirectTo({
		// 		url: '/pages/login'
		// 	})
		//

		// 	uni.reLaunch({
		// 		url: '/pages/login'
		// 	})
		// }

	}
};
